<template>
  <div>
    <h5><i class="fa fa-cubes"></i>  </h5>
<hr>
    <br>
    <em v-if="customers.loading"> ...</em>

    <table class="table" v-if="customers">
      <thead class="thead-light">
        <tr>
        <!--  <th scope="col">#</th> -->
          <th scope="col"></th>
       <!--   <th scope="col"></th> -->
          <th scope="col"></th>
            <th scope="col"></th>
          <th scope="col">.</th>
           <th scope="col"> </th>
          <th scope="col"> </th>
          <th scope="col">.</th>

          <th colspan="1" scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="customer in customers" :key="customer.customerId">
         <!-- <th scope="row">{{customer.customerId }}</th>-->
          <td>{{customer.name }}</td>
         <!-- <td>{{customer.address }}</td> -->
          <td>{{customer.phonNumber1 }}</td>
                     <td> <span v-if="customer.typeBox != ''">{{customer.typeBox }} </span><span v-else> - </span></td>

          <td> <span v-if="customer.numberBox != 0">{{customer.numberBox }} </span><span v-else> - </span></td>
                      <td> <span v-if="customer.dateBills != ''">{{customer.dateBills }} </span><span v-else> - </span></td>

                     <td> <span v-if="customer.expiretDate != '' ">{{customer.expiretDate }} </span><span v-else> - </span></td>
          <td> <span v-if="customer.billsNumber != 0">{{customer.billsNumber }} </span><span v-else> - </span></td>

     
       
              <td>
    <div v-if="customer.billsNumber != 0 && customer.isActive">
            <button @click="confimdelte(customer.billsId)" class="btn btn-danger"> <i class="fa fa-times"></i>  </button>
    </div>
        <div class="text-center" v-else>
              -
            </div>
          </td>
 
        </tr>
      </tbody>
    </table>

     
  </div>
</template>
<script>
import { customerService } from "../../_services/customer.service";

 
export default {
  data() {
    return {
      customers: [],
      isModalShow: false,
      objData: {},
      submitted: false,
      addEditTitle: ""
    };
  },
 

  created() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.customers.loading = true;

    this.getAllBooked();
  },
  methods: {
 
 
    getAllBooked() {
      customerService.getAllBooked().then(data => {
        const resultArray = [];
        for (let key in data) {
          resultArray.push(data[key]);
        }
        this.customers = resultArray;
        console.log(this.customers);
      });
    },
     
  }
};
</script>

<style>
.table {
  border: 1px solid #d3d3d3;
}
.table td,
.table th {
  vertical-align: middle;
}
</style>